
.slider {
  display: block;
  max-width: 545px;
  position: relative;
}
.slider::after {
  content: '';
  display: block;
  padding-bottom: 100%;
  margin-bottom: 45px;
}
.slider .slide0,
.slider .slide1,
.slider .slide2,
.slider .slide3,
.slider .slide4 {
  position: absolute;
  border-radius: 10px;
  box-shadow: 0 6px 12px rgba(20,13,2,0.53);
  margin: 0;
  font-size: 0;
  background: #ccc;
  width: 100%;
}
.slider .slide0::before,
.slider .slide1::before,
.slider .slide2::before,
.slider .slide3::before,
.slider .slide4::before {
  content: '';
  display: block;
  padding-bottom: 99%;
}
.slider .slide0 img,
.slider .slide1 img,
.slider .slide2 img,
.slider .slide3 img,
.slider .slide4 img {
  border-radius: 10px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.slider .slide0 {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  z-index: 1;
  top: -90px;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.slider .slide1 {
  z-index: 4;
  top: 0;
}
.slider .slide2 {
  pointer-events: none;
  z-index: 3;
  top: 30px;
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}
.slider .slide3 {
  pointer-events: none;
  z-index: 2;
  top: 60px;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
.slider .slide4 {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  z-index: 1;
  top: 30px;
  -webkit-transform: scale(0.85);
          transform: scale(0.85);
}
.slider.next-slide .slide0,
.slider.next-slide .slide1,
.slider.next-slide .slide2,
.slider.next-slide .slide3,
.slider.next-slide .slide4 {
  transition: opacity 0.4s, top 0.4s, -webkit-transform 0.4s;
  transition: opacity 0.4s, transform 0.4s, top 0.4s;
  transition: opacity 0.4s, transform 0.4s, top 0.4s, -webkit-transform 0.4s;
}
.slider.next-slide .slide1 {
  top: -90px;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  opacity: 0;
}
.slider.next-slide .slide2 {
  top: 0px;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.slider.next-slide .slide3 {
  top: 30px;
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}
.slider.next-slide .slide4 {
  opacity: 1;
  visibility: visible;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  top: 60px;
}
.slider.prev-slide .slide0,
.slider.prev-slide .slide1,
.slider.prev-slide .slide2,
.slider.prev-slide .slide3,
.slider.prev-slide .slide4 {
  transition: opacity 0.4s, top 0.4s, -webkit-transform 0.4s;
  transition: opacity 0.4s, transform 0.4s, top 0.4s;
  transition: opacity 0.4s, transform 0.4s, top 0.4s, -webkit-transform 0.4s;
}
.slider.prev-slide .slide0 {
  opacity: 1;
  visibility: visible;
  top: 0;
  -webkit-transform: scale(1);
          transform: scale(1);
  z-index: 4;
}
.slider.prev-slide .slide1 {
  top: 30px;
  z-index: 3;
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}
.slider.prev-slide .slide2 {
  top: 60px;
  z-index: 2;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
.slider.prev-slide .slide3 {
  opacity: 0;
  z-index: 1;
  top: 30px;
  -webkit-transform: scale(0.85);
          transform: scale(0.85);
}
.slider.prev-slide .slide4 {
  opacity: 0.1;
  top: 0px;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}
.slider .controls {
  font-size: 0;
  position: absolute;
  bottom: 20px;
  left: 50%;
  margin-left: -120px;
  z-index: 5;
}
.slider .controls button {
  vertical-align: middle;
  min-width: 0;
  width: 60px;
  height: 60px;
  border-radius: 100%;
  padding: 0;
  background: #fff;
  opacity: 0.5;
  transition: background-color 0.3s, opacity 0.3s;
}
.slider .controls button .ico {
  height: 60px;
  width: 60px;
  fill: #1d1d1d;
  transition: fill 0.3s;
}
.slider .controls button:hover {
  background-color: #1d1d1d;
  opacity: 1;
}
.slider .controls button:hover .ico {
  fill: #fff;
}
.slider .controls .count {
  text-shadow: 0 0 5px #000;
  display: inline-block;
  font-size: 22px;
  vertical-align: middle;
  text-align: center;
  width: 120px;
}


