  font-style: italic;
  src: url("./assets/font/lato-lightitalic.woff2") format("woff2"), url("./assets/font/lato-lightitalic.woff") format("woff"), url("./assets/font/lato-lightitalic.ttf") format("truetype");
}
button,
.button {
  display: inline-block;
  cursor: pointer;
  outline: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -ms-touch-action: manipulation;
      touch-action: manipulation;
}
body {
  font-family: Lato, Arial, sans-serif;
  font-size: 17px;
  letter-spacing: 0.34px;
  line-height: 1.53;
  color: #161616;
}
a {
  text-decoration: none;
  position: relative;
  color: #1000fb;
  border-bottom: solid #cfcbff 1px;
  transition: color 0.3s, border 0.2s;
}
a:hover {
  border-bottom: solid #6e64ff 1px;
}
a.pseudo {
  color: inherit;
  border-bottom: dashed #c5c5c5 1px;
}
a.pseudo:visited {
  color: inherit;
  border-bottom: solid #e2e2e2 1px;
}
a.pseudo:hover,
a.pseudo:visited:hover {
  color: inherit;
  border-bottom: dashed #8a8a8a 1px;
}
.link a {
  padding: 0.6em 0 0;
  color: inherit;
  border-bottom: solid transparent 1px;
}
.link a:hover {
  border-bottom: solid #c5c5c5 1px;
}
a:visited {
  color: #6d6d6d;
  border-bottom: solid #e2e2e2 1px;
}
a:visited:hover {
  color: #4c4c4c;
  border-bottom: solid #c5c5c5 1px;
}
.hb-wrapper a,
a.button,
a.more,
a.prev {
  border-bottom: none;
}
.hb-wrapper a:hover,
a.button:hover,
a.more:hover,
a.prev:hover {
  border-bottom: none;
}
h1,
.h1,
h2,
.h2,
h3,
.h3,
.title {
  font-family: AdineuePro, Arial, sans-serif;
  text-transform: uppercase;
  color: #1d1d1d;
}
h1,
.h1 {
  font-size: 36px;
  letter-spacing: 2.2px;
  line-height: 1.33;
}
@media only screen and (max-width: 600px) {
h1,
  .h1 {
    font-size: 28px;
}
}
h3,
.h3 {
  font-size: 15px;
  letter-spacing: 1.2px;
  line-height: 1.73;
}
.title {
  font-size: 36px;
  letter-spacing: 20px;
  line-height: 1.8;
  margin: 40px 0;
}
@media only screen and (max-width: 600px) {
.title {
    font-size: 26px;
    letter-spacing: 10px;
    line-height: 1.6;
}
}
h1.title {
  margin: 60px 0;
}
@media only screen and (max-width: 600px) {
h1.title {
    font-size: 30px;
    line-height: 1.6;
    margin: 30px 0 10px;
}
}
blockquote {
  font-style: italic;
}
blockquote::before,
blockquote::after {
  content: '';
  z-index: -1;
  background: url("~@/assets/svg-for-css/quote.svg");
  background-repeat: repeat-x;
  background-size: 37px 40px;
  opacity: 0.1;
  pointer-events: none;
  display: inline;
  position: absolute;
  -webkit-transform: translate(-30px, -8px);
          transform: translate(-30px, -8px);
  width: 74px;
  height: 40px;
}
@media only screen and (max-width: 600px) {
blockquote::before,
  blockquote::after {
    -webkit-transform: translate(-18px, -2px);
            transform: translate(-18px, -2px);
    width: 55px;
    height: 30px;
}
}
@media print {
blockquote::before,
  blockquote::after {
    content: '';
    display: none;
}
}
blockquote::after {
  -webkit-transform: scale(-1, 1) translate(52px, -8px);
          transform: scale(-1, 1) translate(52px, -8px);
}
@media only screen and (max-width: 600px) {
blockquote::after {
    -webkit-transform: scale(-1, 1) translate(42px, -2px);
            transform: scale(-1, 1) translate(42px, -2px);
}
}
small {
  font-size: 15px;
}
small.extra {
  font-size: 10px;
}
p[data-first-letter] {
  position: relative;
}
p[data-first-letter]::before {
  content: attr(data-first-letter);
  pointer-events: none;
  position: absolute;
  top: -22px;
  left: -4px;
  font-size: 54px;
  line-height: 1;
  color: #1d1d1d;
  opacity: 0.1;
}
p.accent {
  margin: 45px 0;
  padding-left: 20px;
  border-left: solid 1px #6d6d6d;
}
button,
.button {
  font-size: 13px;
  border: none;
  box-sizing: content-box;
  padding: 20px;
  min-width: 160px;
  line-height: 1.07;
  transition: background-color 0.2s, color 0.4s;
}
button.accent,
.button.accent {
  text-transform: uppercase;
  text-align: center;
  font-size: 13px;
  background-color: #ff3b3b;
  margin: 10px;
  color: #fff;
  border-radius: 30px;
}
button.accent:hover,
.button.accent:hover {
  color: #fff;
  background-color: #db3131;
}
button.accent[disabled]:hover,
.button.accent[disabled]:hover {
  background-color: #d2d8da;
}
button[disabled],
.button[disabled] {
  cursor: not-allowed;
  background-color: #d2d8da;
}
.label,
.menu-link {
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 1.2px;
}
a.more,
a.prev {
  font-size: 10px;
  line-height: 2.6;
  letter-spacing: 0.3px;
  text-transform: uppercase;
  transition: color 0.3s, opacity 0.3s;
  color: #6d6d6d;
  padding: 5px 5px 5px 0;
}
a.more::after,
a.prev::after {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  content: '';
  white-space: nowrap;
  background: url("~@/assets/svg-for-css/arrow-more.svg?fill=%236d6d6d");
  width: 16px;
  height: 16px;
  display: inline-block;
  vertical-align: middle;
  margin-left: 8px;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}
a.more:visited:hover,
a.prev:visited:hover,
a.more:hover,
a.prev:hover {
  color: #000;
}
a.more:visited:hover::after,
a.prev:visited:hover::after,
a.more:hover::after,
a.prev:hover::after {
  -webkit-transform: translateX(5px);
          transform: translateX(5px);
}
a.more.action,
a.prev.action {
  color: #f00;
  font-size: 11px;
  font-weight: 900;
  opacity: 0.6;
}
a.more.action::after,
a.prev.action::after {
  background: url("~@/assets/svg-for-css/arrow-more.svg?fill=%23f00");
  opacity: 0.6;
}
a.more.action:hover,
a.prev.action:hover {
  color: #f00;
  opacity: 1;
}
a.prev::after {
  display: none;
}
a.prev::before {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  content: '';
  white-space: nowrap;
  background: url("~@/assets/svg-for-css/arrow-back.svg?fill=%236d6d6d");
  width: 16px;
  height: 16px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 8px;
  margin-top: -2px;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}
a.prev:visited:hover,
a.prev:hover {
  color: #000;
}
a.prev:visited:hover::before,
a.prev:hover::before {
  -webkit-transform: translateX(-5px);
          transform: translateX(-5px);
}
a.prev.action {
  color: #f00;
  font-size: 11px;
  font-weight: 900;
  opacity: 0.6;
}
a.prev.action::before {
  background: url("~@/assets/svg-for-css/arrow-back.svg?fill=%23f00");
  opacity: 0.6;
}
a.prev.action:hover {
  color: #f00;
  opacity: 1;
}
body {
  background: #18141b;
}
body::before {
  content: '';
  display: block;
  height: 0px;
  height: 100px;
  opacity: 0;
}
body::after {
  content: '';
  position: fixed;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background: #fff;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  transition: visibility 0.2s, opacity 0.2s;
}
body.mobile-menu-active,
body.contact-form-active {
  overflow: hidden;
}
body.mobile-menu-active::after,
body.contact-form-active::after {
  pointer-events: all;
  opacity: 0.8;
  visibility: visible;
}


